import React,{Component} from 'react'
import './userDialog.css'
import {signUp,signIn} from './leanCloud'

export default class UserDialog extends Component{
  constructor(props){
    super(props)
    this.state = {
      selected: 'signUp',
      formData:{
        username:'',
        password:''
      }
    }
  }
  switch(e){
    this.setState({
      selected: e.target.value
    })
  }

  changeFormData(e){
    const attr = e.target.name
    let stateCopy = JSON.parse(JSON.stringify(this.state))
    stateCopy.formData[attr] = e.target.value
    this.setState(stateCopy)
  }  
  signUp(e){
    e.preventDefault()
    let {username,password} = this.state.formData
    let success = (user) =>{
      this.props.onSignUp.call(null,user)
    }
    let error = (error) =>{
      switch(error.code){
        case 200:
        alert('')
        break
        case 202:
        alert('')
        break
        default:
        alert(error)
      }
    }
    signUp(username,password,success,error)
  }
  signIn(e){
    e.preventDefault()
    let {username,password} = this.state.formData
    let success = (user) => {
      this.props.onSignIn.call(null,user)
    }
    let error = (error) =>{
      switch(error.code){
        case 210:
        alert('')
        break
        case 211:
        alert('')
        break
        default:
        alert(error)
      }
    }
    signIn(username,password,success,error)
  }
  render(){
    let signUpForm = (
      <form className="signUp" 
      onSubmit={this.signUp.bind(this)}>{/**/}
        <div className="row">
          <label></label>
          <input type="text" name="username" value={this.state.formData.username}
          onChange={this.changeFormData.bind(this)}/>
        </div>
        <div className="row">
          <label></label>
          <input type="password" name="password" value={this.state.formData.password}  
          onChange={this.changeFormData.bind(this)}/>
        </div>
        <div className="row actions">
          <button type="submit"></button>
        </div>
      </form>
    )
    let signInForm = (
      <form className="signIn" 
      onSubmit={this.signIn.bind(this)}>{/**/}
        <div className="row">
          <label></label>
          <input type="text" name="username"  value={this.state.formData.username} 
          onChange={this.changeFormData.bind(this)}/>
        </div>
        <div className="row">
          <label></label>
          <input type="password" name="password" value={this.state.formData.password}
          onChange={this.changeFormData.bind(this)}/>
        </div>
        <div className="row actions">
          <button type="submit"></button>
        </div>
      </form>
    )
    return(
      <div className="UserDialog-Wrapper">
        <div className="UserDialog">
          <nav>
            <label>
            <input type="radio" value="signUp" 
              checked={this.state.selected === "signUp"}
              onChange={this.switch.bind(this)}/></label>
            <label>
            <input type="radio" value="signIn" 
            checked={this.state.selected === "signIn"}
            onChange={this.switch.bind(this)}/></label>
          </nav>
          <div className="panels">
          {this.state.selected === 'signUp'? signUpForm : null}
          {this.state.selected === 'signIn'? signInForm : null}
          </div>
        </div> 
      </div>
    )
  }
}


